import { ChevronLeftIcon, ChevronRightIcon } from "@heroicons/react/20/solid";
import { useLocation } from "react-router-dom";

export default function Pagination({
  numbers,
  prePageFunc,
  changeCPage,
  nextPageFunc,
  currentPage,
  recordsPerPage,
  lastIndex,
  firstIndex,
  nPages,
  records,
}) {
  const location = useLocation();
  console.log("TOTALPAGES " + nPages);
  console.log("FIRST " + firstIndex);
  console.log("LAST " + lastIndex);
  console.log("RECORDS " + lastIndex);
  console.log("CURRENT " + currentPage);
  return (
    <div className="flex items-center justify-between border-t border-gray-200 bg-white px-4 py-3 sm:px-6 ">
      <div className="flex flex-1 justify-between sm:hidden">
        <p className="relative inline-flex items-center rounded-md border border-gray-300 bg-white px-4 py-2 text-sm font-medium text-gray-700 hover:bg-gray-50 cursor-pointer">
          Previous
        </p>
        <p className="relative ml-3 inline-flex items-center rounded-md border border-gray-300 bg-white px-4 py-2 text-sm font-medium text-gray-700 hover:bg-gray-50 cursor-pointer">
          Next
        </p>
      </div>
      <div className="hidden sm:flex sm:flex-1 sm:items-center sm:justify-between max-w-7xl mx-auto">
        <div
          className={`${
            location.pathname === "/dashboard/all-orders" && "hidden"
          }`}
        >
          <p className="text-sm text-gray-700 ">
            Showing <span className="font-medium">{currentPage}</span> to{" "}
            <span className="font-medium">{firstIndex}</span> of{" "}
            <span className="font-medium">{nPages}</span> results
          </p>
        </div>
        <div>
          <nav
            className="isolate inline-flex -space-x-px rounded-md shadow-sm"
            aria-label="Pagination"
          >
            <p
              onClick={prePageFunc}
              className="relative inline-flex items-center rounded-l-md px-2 py-2 text-gray-400 ring-1 ring-inset ring-gray-300 hover:bg-gray-50 focus:z-20 focus:outline-offset-0 cursor-pointer"
            >
              <span className="sr-only">Previous</span>
              <ChevronLeftIcon className="h-5 w-5" aria-hidden="true" />
            </p>
            {numbers.map((page, index) => (
              <div key={index}>
                <p
                  className={`${
                    currentPage === page
                      ? "cursor-pointer relative z-10 inline-flex items-center bg-indigo-600 px-4 py-2 text-sm font-semibold text-white focus:z-20 focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-indigo-600"
                      : "cursor-pointer relative inline-flex items-center px-4 py-2 text-sm font-semibold text-gray-900 ring-1 ring-inset ring-gray-300 hover:bg-gray-50 focus:z-20 focus:outline-offset-0"
                  } `}
                  onClick={() => changeCPage(page)}
                >
                  {page}
                </p>
              </div>
            ))}
            <p
              onClick={nextPageFunc}
              className="relative inline-flex items-center rounded-r-md px-2 py-2 text-gray-400 ring-1 ring-inset ring-gray-300 hover:bg-gray-50 focus:z-20 focus:outline-offset-0 cursor-pointer"
            >
              <span className="sr-only">Next</span>
              <ChevronRightIcon className="h-5 w-5" aria-hidden="true" />
            </p>
          </nav>
        </div>
      </div>
    </div>
  );
}
